<template>
    <div class="swiper-container">
        <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(key,i) in deta" :key="i">
                <router-link :to="'/photo_page?id='+key.id">
                    <div>
                        <img src="/static/img/bg.png" alt />
                        <p :style="{backgroundImage: 'url(' + key.img_url + ')'}"></p>
                    </div>
                    <p class="sw-title">{{key.title}}</p>
                    <p class="sw-time">{{key.issueTime | dateFormat}}</p>
                </router-link>
            </div>
        </div>
  </div>
</template>

<script>
//   :deta="data"  
//import myHead from "./head.vue"
import Swiper from "swiper";
export default {
   components: {
  },
  data() {
    return {
        name : '',
        
       
    };
  },
  props: {
    //
    deta: {
      default: function() {
        return '';
      }
    }
  },
  beforeCreate(){
    //data
  },
  created() {
    //,,data
  },
  mounted() {
    //
    let  sw = new Swiper(".swiper-container", {
            // slide
            centeredSlides: false,
            //slides
            slidesPerView: 1,
            //slides
            slidesPerGroup: 1,
            //sidespx
            spaceBetween: 10,
            breakpoints: {  //5.0 
                1100: {
                    slidesPerView: 3,
                },
                750: {
                    slidesPerView: 2,
                }
            },
            // 
            loop: true,
            //
            freeMode: false,
            //
            autoplay: true,
        })
    this.fuSwiper(".swiper-container", sw);
  },
  methods: {
    //
    fuSwiper: function(el, swipEl) {
      $(el)
        .mousemove(function() {
          swipEl.autoplay.stop();
        })
        .mouseleave(function() {
          swipEl.autoplay.start();
        });
    },
   
  }
};
</script>

<style scoped>
.swiper-slide a{
    display: block;
    padding: .5rem;
    background: #fff;  
    border-radius: 3px;
}
.swiper-slide a div{
    position: relative;
}
.swiper-slide a div img{
    display: block;
    width: 100%;
}
.swiper-slide a div p{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover; 
}
.sw-title{
    font-size: 1rem;
    color: #333;
    text-align: center;
    margin-top: 5px;
    transition: all .3s;
    font-weight: bold;
}
.sw-time{
    text-align: center;
    font-size: .8rem;
    color: #f00;
    margin-top: 5px;
}
.swiper-slide a:hover .sw-title{
    color: #f00;
}
</style>



