<template>
  <div>
    <m_loading v-if="hideLoading"></m_loading>
    <div class="swiper-photo animated fadeInDown">
      <!-- <swiper_video  :deta="videos"></swiper_video> -->
      <div class="photo-title"></div>
      <swiper_photo v-if="isRouterAlive" :deta="phptoCon"></swiper_photo>
    </div>
    <div id="sub-content" class="sub-content">
      <div class="sub-text">
        <div class="sub-title"></div>
        <ul class="a-content" :style="{minHeight: max_height+ 'px'}">
          <li ref="item" class="item" v-for="(key,i) in article" :key="i" :style="{left:item_coor[i].left + 'px',top:item_coor[i].top + 'px'}">
            <div>
              <router-link :to="key.bg_class == 1 ? '/life_page?id='+key.id : '/it_page/it'+key.id+'?sm_class='+key.sm_class+'&id='+key.id">
                <p class="a-img"><img :src="key.title_url" alt=""></p>
                <p class="a-title">{{key.title}}</p>
                <p class="a-time">{{key.issueTime  | dateFormat}}</p>
                <p class="a-text">{{key.intro}}</p>
                <p class="a-gauze"></p>
              </router-link> 
            </div>
          </li>
        </ul>
        <div class="paging">
          <p class="prev" @click="paging(at_num-=1)"></p>
          <p class="present">{{at_num}}</p>
          <p class="next" @click="paging(at_num+=1)"></p>
          <p class="sum">{{sum_num}}</p>
        </div>
      </div>
      <div class="person">
        <div class="sub-title"></div>
        <right_nav></right_nav>
      </div>
    </div>
  </div>
</template>

<script>
//   :deta="data"  
// import swiper_video from "@/components/basis/swiper_video"
import swiper_photo from "@/components/basis/swiper_photo"
import right_nav from "@/components/basis/right_nav"
import m_loading from "@/components/basis/loading"
//swiper
export default {
   components: {
    //  swiper_video,
     swiper_photo,
     right_nav,
     m_loading,

  },
  data() {
    return {
      hideLoading:true,
      at_num:1,
      sum_num:1,
      time : '',
      videos:[
        {id:1,img_url:'/static/img/swiper/swp1.png',video_url:'/static/img/swiper/swp1.mp4',title:'',time:'2019-10-31'},
        {id:2,img_url:'/static/img/swiper/swp1.png',video_url:'/static/img/swiper/swp1.mp4',title:'',time:'2019-10-31'},
        {id:3,img_url:'/static/img/swiper/swp1.png',video_url:'/static/img/swiper/swp1.mp4',title:'',time:'2019-10-31'},
        {id:4,img_url:'/static/img/swiper/swp1.png',video_url:'/static/img/swiper/swp1.mp4',title:'',time:'2019-10-31'},
        {id:5,img_url:'/static/img/swiper/swp1.png',video_url:'/static/img/swiper/swp1.mp4',title:'',time:'2019-10-31'}
      ],
      phptoCon:'',
      //
      article:[],
      col:2,  //
      col_height:[], //
      col_width:'', //item
      item_height:[],//item 
      item_coor:[],  //item
      item_btm:10,  //
      max_height:'', //
      screenWidth: document.body.clientWidth, //
      count: 0,
      isRouterAlive:false
     
    };
  },
  props: {
    //
    deta: {
      default: function() {
        return '';
      }
    }
  },
  created() {
    //,,data
    //
    //this.getArticle();
    this.getPage();
    this.getPhoto()
  },
  mounted() {
    //
    const that = this;
    //
  window.onresize = () => {
      return (() => {
        window.screenWidth = document.body.clientWidth
        that.screenWidth = window.screenWidth
        that.quireCoord();
    })()}
  },
  methods: {
    //
    //clickdrawFeatures
    //10
    getPhoto:function(){
      //
      let that = this;
      this.axios({
        method: 'get',
        url:'http://localhost:3306/photo/listSw',
        data:''
      }).then((res)=>{
        that.phptoCon = res.data 
        that.reload ()
      })
      .catch(function (error) {
          console.log(error);
        });
    },
    //
    preload: function() {
      //
      this.item_coor = []
      for (let i = 0;i < this.article.length;i++){
        this.item_coor.push({left:'0',top:'0'}) 
      } 
      //
      this.count = 0;
      for (let img of this.article) {
        let image = new Image()
        image.src = img.title_url
        image.onload = () => {
          this.count++
        }
      }
    },
    //
    getPage:function(){
      //
      this.axios({
        method: 'get',
        url:'http://localhost:3306/article/list_pno',
        data:''
      }).then((res)=>{
        if(res.data.length>0){
          this.sum_num = Math.ceil(res.data[0]['COUNT(*)']/10)
          this.paging();
        }
      })
    },
    //
    paging:function(a){
      //
      document.body.scrollIntoView();
      this.hideLoading = true;
      //
      if(this.at_num == 1){
        $('.prev').hide()
        if(this.sum_num>this.at_num){
          $('.present').show()
          $('.next').show()
          $('.sum').show()
        }else{
          $('.present').hide()
          $('.next').hide()
          $('.sum').hide()
        }
      }else{
        $('.prev').show()
        $('.sum').show()
        if(this.sum_num>this.at_num){
          $('.next').show()
        }else{
          $('.next').hide()
        }
      }
      this.getArticle();
    },
    //
    getArticle:function(){
      let url = 'http://localhost:3306/article/list'
      //
      let pno = this.at_num
      let that = this
      this.axios.get(url, {
          params: {
            pno: pno
          }
        })
        .then(function (result) {
          if(result.data.length>0){
            that.article = result.data
            that.preload()
          } else{
            alert('')
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    //
    quireCoord:function(){
      this.addWindow();
      this.col_width = $(".item").width();//item
      let arr = [];
      let str = []
      $(".item").each(function(){
        arr.push(Math.ceil($(this).height()));
        str.push({left:'0',top:'0'})  //item
      });
      this.item_height = [].concat(arr);//item
      for(let i = 0;i<this.col;i++){
        this.col_height[i] = 0; //
      }
      this.addCoord()
    },
    addCoord:function(){
      for(let a = 0; a<this.article.length; a++){
        var min = Math.min.apply(Math,this.col_height); //
        var sub = this.col_height.indexOf(min)  //
        this.item_coor[a].left = this.col_width*sub  //itemleft
        this.item_coor[a].top = this.col_height[sub];  //itemtop
        this.col_height[sub] += this.item_height[a];   //  item
        this.col_height[sub] += this.item_btm; //  
        var max = Math.max.apply(Math,this.col_height); 
        this.max_height = max;
      }
    },
    //
    addWindow:function(){
      if(this.screenWidth < 750){
          this.col = 1;
      }else{
        this.col = 2;
      }
    },
    //
    reload () {
      this.isRouterAlive = false
      this.$nextTick(() => (this.isRouterAlive = true))
    }
  },
  watch: {
    //
    count: function(val) {
      if(val  == this.article.length && val != 0){
        let that = this
        let timeId = setTimeout(function(){
          that.hideLoading = false;
        },300)
        this.quireCoord();
      }
    }
  }
};
</script>

<style scoped>
  .swiper-photo{
    padding: 10px;
    background: rgba(250, 250, 250, .5);
  }
  .photo-title{
    font-size: 1.2rem;
    line-height: 3;
    font-weight: bold;
    color: #000;
    letter-spacing: 1px;
  }
  .sub-content{
    margin-top: 10px;
    padding: 0 10px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    background: rgba(238, 238, 238, .5);
  }

  .sub-title{
    font-size: 1.2rem;
    line-height: 3;
    font-weight: bold;
    color: #000;
    letter-spacing: 1px;
  }
  .sub-text{
    width: 68%;
  }
  .person{
    width: 30%;
  }

  .a-content{
    margin-top: -.5rem;
    width: 100%;
    position: relative;
  }

  .item{
      z-index: 10;
      transition: 0.8s;
      width: 50%;
      position: absolute;
  }
  .item div{
    width: 100%;
    box-sizing: border-box;
    padding: .5rem ;
  }
 
  .item div a{
    display: block;
    background: #fafafa;
    padding: .5rem;
    border-radius: 3px;
    position: relative;
  }
  .a-img{
    overflow: hidden;
  }
  .a-img img{
    width: 100%;
    display: block;
    transition: all .5s;
  }
  
  .a-title{
    font-size: 1rem;
    line-height: 2;
    color: #333;    
    font-weight: bold;
    letter-spacing: 1px;
    transition: all .3s;
  }
  .a-time{
    font-size: .8rem;
    line-height: 1.5;
    color: #f00;    
    padding: 0 1rem;
    letter-spacing: 1px;
  }
  .a-text{
    padding: 0 1rem;
    color: #666;
    line-height: 1.5;
    font-size: .8rem;
    letter-spacing: 1px;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    transition: all .3s;
  }
  .a-gauze{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-box-shadow: rgba(0,0,0,0.5) 0px .3rem .3rem;
    -moz-box-shadow: rgb(0,0,0,0.5) 0px .3rem .3rem;
    box-shadow: rgba(0,0,0,0.5) 0px .3rem .3rem;
    opacity: 0;
    transition: all .5s;
    z-index: -1;
  }
  .item div:hover a .a-gauze{
    opacity: 1;
  }
  .item div:hover a .a-title{
    color: #f00;
  }
  .item div:hover a .a-text{
    color: #000;
  }
  .item div:hover a .a-img img{
    transform: scale(1.1);
  } 

  /*  */
  .paging{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0 30px 10px;
    letter-spacing: 1px;
    font-size: .9rem;
    color: #333;
  }
  .paging p{
    margin-left: 10px;
  }
  .prev,.next{
    color: #000;
    cursor: pointer;
    font-weight: bold;
  }
  @media (max-width: 1200px) {
    .person{
      display: none;
    }
    .sub-text{
      width: 100%;
    }
  }
  @media (max-width: 750px) {
  .sub-content{
    padding: 10px 0;
  }
  .swiper-video{
    padding: 10px 10px;
    background: #fafafa;
    animation: bounceInDown .5s;
  }
  .item{
    width: 100%;
  }
  }
</style>



